import logo from './logo.svg';
import React, { useState } from 'react';
import './App.css';
import Cont1 from './component/content1';
import Cont2 from './component/content2';

function App() {
  let conArr = {
    'Cont1': <Cont1 />,
    'Cont2': <Cont2 />
  };
  let [cpnt, setCpnt] = useState(conArr['Cont1']);

  function nav(e) {
    let menuTxt = e.target.textContent;
    setCpnt(conArr[menuTxt]);
  }

  return (
    <>
      <header onClick={nav}>
        <a>Cont1</a>
        <a>Cont2</a>
      </header>
      <main>
        <div>
          {cpnt}
        </div>
      </main>
    </>
  );
}

export default App;
